.image-container {
  width: 100%;
  margin: auto;
  padding: 6rem 0;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(rgba(0, 0, 0, 0.55), rgba(0, 0, 0, 0.55)), url("../../Assets/about_image.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  text-align: center;
}
.image-container .MuiGrid-item {
  padding: 0px;
}
.image-container p {
  font-size: 6.2rem;
  background: -webkit-linear-gradient(90deg, #c3a2fe, #9358fd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 500;
  font-family: "Poppins";
}
.image-container h5 {
  font-size: 1.6rem;
  font-weight: 300;
  font-family: "Poppins";
  color: rgb(255, 255, 255);
}
.image-container i {
  font-size: 0.7rem;
  font-style: normal;
  font-family: "Century Gothic";
  color: rgb(255, 255, 255);
}
.image-container .social-links-container {
  margin-top: 2rem;
}
.image-container .social-links-container .speedDial-container .MuiFab-root {
  background: linear-gradient(90deg, #c3a2fe, #9358fd);
  text-transform: none;
  font-family: "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}
.image-container .social-links-container .speedDial-container #SpeedDialforlinks-actions {
  flex-direction: row;
  margin: 0;
  padding: 0;
}

@media (min-width: 363px) {
  .image-container {
    height: calc(100vh - 48px);
  }
}
@media (min-width: 601px) {
  .image-container {
    height: calc(100vh - 64px);
  }
}
@media (max-width: 362px) {
  .image-container {
    height: calc(100vh - 56px);
  }
}
@media (max-width: 768px) {
  .image-container {
    padding: 4rem 0;
  }
  .image-container .image-box {
    width: 12rem;
    height: 12rem;
  }
  .image-container p {
    font-size: 3.4rem;
  }
}
.education_container .image_container {
  height: auto;
  padding: 16px;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../Assets/book_image.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.education_container .image_container h5 {
  font-size: 2rem;
  text-align: center;
  color: #fff;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.education_container .accordion_container .MuiAccordionSummary-root {
  background: #eee;
}
.education_container .accordion_container .Mui-expanded p {
  color: #63a4ff;
}
.education_container .accordion_container p {
  color: rgb(70, 70, 70);
  font-size: 1.2rem;
}
.education_container .accordion_container ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.education_container .accordion_container ul li {
  color: #818181;
}

@media (min-width: 600px) and (max-width: 799px) {
  .education_container .image_container h5 {
    font-size: 1.6rem;
  }
}
@media (max-width: 599px) {
  .education_container .image_container h5 {
    font-size: 1.6rem;
    padding: 4rem 0;
  }
}
.footer_container {
  background: rgb(0, 0, 0);
}
.footer_container .MuiGrid-item {
  padding: 2rem 0;
  text-align: center;
}
.footer_container .MuiGrid-item .icon {
  color: #fff;
}
.footer_container .MuiGrid-item p {
  color: #fff;
}/*# sourceMappingURL=style.css.map */